import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import VoteIcon from '@mui/icons-material/HowToVote';

const pages = ['Secoes', 'GitHub', 'TSE'];

function ResponsiveAppBar() {

  return (
    <AppBar position="static" sx={{display: {xs:'none', md: 'flex' }}}>
      <Container maxWidth="xl" >
        <Toolbar disableGutters>
          <VoteIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Eleies
          </Typography>
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: '#',
              textDecoration: 'none',
            }}
          >
            Eleies
          </Typography>
          <Box sx={{ flexGrow: 1, display: { md: 'flex' } }}>
              <Button
              href='https://dadosabertos.tse.jus.br/dataset/'
                key={1}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                TSE
              </Button>

              <Button
                href='https://github.com/gSiviero/eleicoes'
                key={2}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                GitHub
              </Button>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;
