import DataTable from "react-data-table-component";
// import {Item}  from "react"
import {
  Card,
  Box,
  Container,
  TextField,
  CardHeader,
  Button,
  FormControl,Select,InputLabel,MenuItem
} from "@mui/material";
import * as React from "react";
import CsvDownload from "react-json-to-csv";

const columns = [
  {
    name: "Estado",
    field: "UF",
    sortable: true,
    selector: (row) => row.UF,
  },
  {
    name: "Turno",
    field: "Turno",
    sortable: true,
    selector: (row) => row.Turno,
  },
  {
    name: "Municipio",
    field: "Municipio",
    sortable: true,
    selector: (row) => row.Municipio,
  },
  {
    name: "Zona",
    field: "Zona",
    sortable: true,
    selector: (row) => row.Zona,
  },
  {
    name: "Seo",
    field: "Secao",
    sortable: true,
    selector: (row) => row.Secao,
  },
  {
    name: "Lula (%)",
    field: "LulaP",
    sortable: true,
    selector: (row) => `${parseFloat(row.LulaP).toFixed(2)}%`,
  },
  {
    name: "Bolsonaro (%)",
    field: "BolsonaroP",
    sortable: true,
    selector: (row) => `${parseFloat(row.BolsonaroP).toFixed(2)}%`,
  },
  {
    name: "Outros (%)",
    field: "OutrosP",
    sortable: true,
    selector: (row) => `${parseFloat(row.OutrosP).toFixed(2)}%`,
  },
  {
    name: "Total",
    field: "Total",
    sortable: true,
    selector: (row) => row.Total,
  },
];

function Eleicoes() {
  const [data, updateData] = React.useState([]);
  const [turno, updateTurno] = React.useState(1);
  const [uf, updateUf] = React.useState(null);
  const [municipio, updateMunicipio] = React.useState(null);
  const [zona, updateZona] = React.useState(null);
  const [secao, updateSessao] = React.useState(null);
  const [page, updatePage] = React.useState(1);
  const [pageSize, updatePageSize] = React.useState(15);
  const [numberOfRows, updatenumberOfRows] = React.useState(15);
  const [loading, updateLoading] = React.useState(true);
  const [sort, updateSort] = React.useState({ column: "UF", direction: "asc" });

  const filtrar = () => {
    updateLoading(true);
    try {
      fetch("http://localhost:3001/sessoes", {
        method: "POST",
        body: JSON.stringify({
          filter: {turno, uf, municipio, zona, secao },
          page,
          pageSize,
          sort,
        }),
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      })
        .then((response) => response.json())
        .then((d) => {
          updateData(d.data);
          updatenumberOfRows(d.numberOfRows);
          updateLoading(false);
        });

      //   updateData(result);
    } catch (err) {
      console.log(err);
    } finally {
    }
  };

  React.useEffect(filtrar, [sort, page, pageSize]);

  return (
    <Container>
      <Box sx={{ padding: 5, paddingBottom: 0 }}>
        <Card sx={{ boxShadow: 3 }}>
          <CardHeader sx={{ textAlign: "left" }} title={"Filtros"} />
          <Box
            sx={{
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "flex-start",
            }}
          >
            <div>
              <FormControl sx={{ m: 1, width: "15ch" }}>
                <InputLabel id="demo-simple-select-label">Turno</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={turno}
                  label="Turno"
                  onChange={(a) => updateTurno(a.target.value)}
                >
                  <MenuItem value={1}>Primeiro</MenuItem>
                  <MenuItem value={2}>Segundo</MenuItem>
                </Select>
              </FormControl>
              <TextField
                value={uf}
                onChange={(event) => updateUf(event.target.value)}
                label="Estado"
                id="outlined-start-adornment"
                sx={{ m: 1, width: "25ch" }}
              />
              <TextField
                value={municipio}
                onChange={(event) => updateMunicipio(event.target.value)}
                label="Municipio"
                id="outlined-start-adornment"
                sx={{ m: 1, width: "25ch" }}
              />
              <TextField
                value={zona}
                onChange={(event) => updateZona(event.target.value)}
                label="Zona"
                id="outlined-start-adornment"
                sx={{ m: 1, width: "25ch" }}
              />
              <TextField
                value={secao}
                onChange={(event) => updateSessao(event.target.value)}
                label="Seo"
                id="outlined-start-adornment"
                sx={{ m: 1, width: "25ch" }}
              />
            </div>
          </Box>
          <Box
            sx={{ display: "flex", flexWrap: "wrap", justifyContent: "center" }}
          >
            <Button
              sx={{ m: 1, width: "110ch" }}
              variant="contained"
              onClick={filtrar}
            >
              Filtrar
            </Button>
          </Box>
        </Card>
      </Box>
      <Box sx={{ padding: 5, height: "100%" }}>
        <Card sx={{ boxShadow: 3 }}>
          <CardHeader
            style={{ textAlign: "left" }}
            title={"Sesses"}
            action={
              <Button variant="contained">
                <CsvDownload
                  data={data}
                  style={{
                    backgroundColor: "transparent",
                    border: "none",
                    color: "white",
                  }}
                >
                  Download CSV
                </CsvDownload>
              </Button>
            }
          />
          <DataTable
            fixedHeader={true}
            columns={columns}
            data={data}
            pagination
            progressPending={loading}
            paginationServer
            paginationTotalRows={numberOfRows}
            onChangeRowsPerPage={updatePageSize}
            onChangePage={updatePage}
            onSort={(s, b) => {updateSort({ column: s.field, direction: b });
            }}
          />
        </Card>
      </Box>
    </Container>
  );
}

export default Eleicoes;
